//importing the necassary modules
import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
//some bootstrap components
import { Form, Button, Row, Col } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
//some actions and components
import Message from '../components/Message'
import Loader from '../components/Loader'
import FormContainer from '../components/FormContainer'
import { login } from '../actions/userActions'
import family from '../assets/family.png'

//this is the user login screen, here a user can login to their user profile that they created for the application

const LoginScreen = ({ location, history }) => {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  const dispatch = useDispatch()

  const userLogin = useSelector((state) => state.userLogin)
  const { loading, error, userInfo } = userLogin

  const redirect = location.search ? location.search.split('=')[1] : '/'

  useEffect(() => {
    if (userInfo) {
      history.push(redirect)
    }
  }, [history, userInfo, redirect])

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(login(email, password))
  }

  //this page also make use of a basic form to get the user to input their login details.
  return (
    <div>
      <div className="signup-bg">
        <h1>We've <br />  Got you <br />Covered</h1>
        <img className="sign-image" src={family} alt="" />
      </div>
      <div className="signup">
        <div className="content">
          <FormContainer>
            <h1 className="login-head">Welcome Back!</h1>
            {error && <Message variant='danger'>{error}</Message>}
            {loading && <Loader />}
            <Form onSubmit={submitHandler}>
              <Form.Group controlId='email'>
                <Form.Label>Email Address</Form.Label>
                <Form.Control
                  type='email'
                  placeholder='Enter email'
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                ></Form.Control>
              </Form.Group>

              <Form.Group controlId='password'>
                <Form.Label>Password</Form.Label>
                <Form.Control
                  type='password'
                  placeholder='Enter password'
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></Form.Control>
              </Form.Group>

              <Button className="signup-button" type='submit' variant='primary'>
                Sign In
              </Button>
            </Form>
            <div className="sign-bottom">
              <Row className='py-3'>
                <Col>
                  Dont have an account?{' '}
                  <Link to={redirect ? `/register?redirect=${redirect}` : '/register'}>
                    Sign up
                  </Link>
                </Col>
              </Row>
            </div>
          </FormContainer>
        </div>
      </div>
    </div>
  )
}

export default LoginScreen
